<template>
  <div class="intro">
    <div class="left-img">
      <div class="bg-img">
        <img src="/static/images/intro3.png" alt />
        <div class="cover">
          <div class="hobby-text" >
            <p></p>
            <p> </p>
            <p>   </p>
            <p> </p>
            <p> </p>
          </div>
          <div class="phone-hobby-text">
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
          </div>
        </div>
      </div>
      <div class="bg-img">
        <img src="/static/images/intro5.png" alt />
        <div class="cover">
          <div class="hobby-text" >
            <p></p>
            <p> <span>{{ date | oldDate }}</span></p>
            <p> <span>{{ date | formaDate }}</span></p>
            <p> <span style="font-size:18px">{{ date | timeFn1 }}</span> </p>
            <p> <span style="font-size:18px">{{ date | timeFn2 }}</span> </p>
          </div>
          <div class="phone-hobby-text">
            <p></p>
            <p> <span>{{ date | oldDate }}</span></p>
            <p> <span>{{ date | formaDate }}</span></p>
            <p> <span style="font-size:16px">{{ date | timeFn1 }}</span> </p>
            <p> <span style="font-size:16px">{{ date | timeFn2 }}</span> </p>
          </div>
        </div>
      </div>
    </div>
    <div class="right-img">
      <div class="bg-img">
        <img src="/static/images/intro1.png" alt />
        <div class="cover">
          <div class="intro-text">
            <p></p>
            <p> </p>
            <p> 19960908</p>
            <p> </p>
            <p> </p>
            <p> </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
var padaDate = function(value) {
  return value < 10 ? "0" + value : value;
};
export default {
  name: "home",
  data() {
    return {
      code: "",
      report_info: {},
      date: new Date()
    };
  },
  filters: {
    //
    formaDate: function(value) {
      //
      var date = new Date();
      //console.log(date)
      var year = date.getFullYear(); //
      var month = padaDate(date.getMonth() + 1); //
      var day = padaDate(date.getDate()); //
      var hours = padaDate(date.getHours()); //
      var minutes = padaDate(date.getMinutes()); //
      var seconds = padaDate(date.getSeconds()); //
      //
      return (
        year +
        "-" +
        month +
        "-" +
        day +
        " " +
        hours +
        ":" +
        minutes +
        ":" +
        seconds 
      );
    },
    oldDate: function(value){
      var t1="2019/12/28 11:00:00"; //
      var date = new Date(t1);
      //console.log(date)
      var year = date.getFullYear(); //
      var month = padaDate(date.getMonth() + 1); //
      var day = padaDate(date.getDate()); //
      var hours = padaDate(date.getHours()); //
      var minutes = padaDate(date.getMinutes()); //
      var seconds = padaDate(date.getSeconds()); //
      //
      return (
        year +
        "-" +
        month +
        "-" +
        day +
        " " +
        hours +
        ":" +
        minutes +
        ":" +
        seconds 
      );
    },
    timeFn1: function(value)  {//di
      //
      var t1="2019/12/28 11:00:00"; //
      var dateBegin = new Date(t1);
      var dateEnd = new Date();//
      var dateDiff = dateEnd.getTime() - dateBegin.getTime();//
      var dayDiff = Math.floor(dateDiff / (24 * 3600 * 1000));//
      var leave1=dateDiff%(24*3600*1000)    //
      var hours=Math.floor(leave1/(3600*1000))//
      //
      var leave2=leave1%(3600*1000)    //
      var minutes=Math.floor(leave2/(60*1000))//
      //
      var leave3=leave2%(60*1000)      //
      var seconds=Math.round(leave3/1000)
      //console.log("  "+dayDiff+" "+hours+" "+minutes+" "+seconds+" ")
      //console.log(dateDiff+"",dayDiff+"",leave1+""
        //  ,hours+"",minutes+"",seconds+"");
      return (
        dayDiff
      );
    },
    timeFn2: function(value)  {//di
      //
      var t1="2020/01/20 11:30:00"; //
      var dateBegin = new Date(t1);
      var dateEnd = new Date();//
      var dateDiff = dateEnd.getTime() - dateBegin.getTime();//
      var dayDiff = Math.floor(dateDiff / (24 * 3600 * 1000));//
      var leave1=dateDiff%(24*3600*1000)    //
      var hours=Math.floor(leave1/(3600*1000))//
      //
      var leave2=leave1%(3600*1000)    //
      var minutes=Math.floor(leave2/(60*1000))//
      //
      var leave3=leave2%(60*1000)      //
      var seconds=Math.round(leave3/1000)
      //console.log("  "+dayDiff+" "+hours+" "+minutes+" "+seconds+" ")
      //console.log(dateDiff+"",dayDiff+"",leave1+""
          //,hours+"",minutes+"",seconds+"");
      return (
        dayDiff
      );
    }
  },
  props: {
    //
    map: {
      //type:String
    }
  },
  methods: {
    //
    //clickdrawFeatures
  },
  created() {
    //
  },
  mounted() {
    //
    $(".bg-img").bind("mouseenter mouseleave", function(e) {
      var this_slidiv = $(this).find(".cover");
      var offset = $(this).offset();
      var w = $(this).width(); //silde
      var h = $(this).height(); //slide
      var x = e.pageX; //
      var y = e.pageY; //
      var l = offset.left; //slide
      var t = offset.top; //slide
      var a = Math.abs(x - l); //slide
      if (a == 0) {
        a = 1;
      }
      var b = Math.abs(y - t); //slide
      if (b == 0) {
        b = 1;
      }
      var n = 1; //     1 2 3 4
      if (a / b > w / h) {
        //n = 1 || 2
        if (Math.abs(w - a) / Math.abs(b) > w / h) {
          n = 1;
        } else {
          n = 2;
        }
      } else {
        //n = 3 || 4
        if (Math.abs(w - a) / Math.abs(b) > w / h) {
          n = 4;
        } else {
          n = 3;
        }
      }
      //console.log(n)
      //console.log(b)
      if (e.type == "mouseenter") {
        switch (n) {
          case 1:
            this_slidiv.css({
              top: -h,
              left: "0px"
            });
            break;
          case 2:
            this_slidiv.css({
              top: "0px",
              left: w
            });
            break;
          case 3:
            this_slidiv.css({
              top: h,
              left: "0px"
            });
            break;
          case 4:
            this_slidiv.css({
              top: "0px",
              left: -w
            });
            break;
        }

        this_slidiv.stop(true, true).animate(
          {
            top: "0px",
            left: "0px"
          },
          300
        );
      } else if (e.type == "mouseleave") {
        switch (n) {
          case 1:
            this_slidiv.stop(true, true).animate(
              {
                top: -h
              },
              300
            );
            break;
          case 2:
            this_slidiv.stop(true, true).animate(
              {
                left: w
              },
              300
            );
            break;
          case 3:
            this_slidiv.stop(true, true).animate(
              {
                top: h
              },
              300
            );
            break;
          case 4:
            this_slidiv.stop(true, true).animate(
              {
                left: -w
              },
              300
            );
            break;
        }
      }
    });
    //this.formaDate();
    //
    var _this = this;
    this.timeId = setInterval(function() {
      _this.date = new Date();
    }, 1000);
  },
  beforeDestroy: function() {
    //
    if (this.timeId) {
      clearInterval(this.timeId);
    }
  },
};
</script>

<style scoped>
img {
  width: 100%;
}

.intro {
  margin: 50px 0 20px;
  position: relative;
}
.left-img {
  width: 32%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  top: 0;
  right: 0;
}
.right-img {
  width: 66%;
}
.bg-img {
  position: relative;
  overflow: hidden;
  border-radius: 4px;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.2);
}
.cover {
  color: #fff;
  width: 100%;
  height: 100%;
  background: rgba(29, 29, 29, 0.5);
  text-align: center;
  position: absolute;
  top: 0;
  left: -100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.intro-text {
  width: 75%;
  height: 70%;
  text-align: left;
  font-size: 24px;
  line-height: 2;
  letter-spacing: 1px;
  overflow: hidden;
}
.intro-text p:first-child {
  font-size: 35px;
  margin-bottom: 10px;
}
.intro-text p:not(:first-child){
  text-indent: 48px;
  white-space: nowrap;
}
.hobby-text {
  display: block;
  width: 85%;
  height: 85%;
  text-align: left;
  font-size: 16px;
  letter-spacing: 1px;
  overflow: hidden;
}
.hobby-text p:first-child {
  font-size: 25px;
  margin-bottom:5px;
  line-height: 2;
}
.hobby-text p:not(:first-child){
  text-indent: 40px;
  margin-bottom: 5px;
  line-height: 1.5;
}
.hobby-text span{
  font-weight: bold;
}
.phone-hobby-text{
  display: none;
}
@media (max-width: 750px) {
  .left-img {
    width: 100%;
    flex-direction: row;
    position: relative;
    margin-bottom: 2%;
  }
  .left-img .bg-img {
    width: 49%;
  }
  .right-img {
    width: 100%;
  }
  .intro {
    margin: 10px 0;
  }
  .intro-text {
    width: 80%;
    height: 80%;
    font-size: 18px;
  }
  .intro-text p:first-child {
    font-size: 24px;
    margin-bottom: 5px;
  }
  .intro-text p:not(:first-child){
    text-indent: 30px;
  }
  .hobby-text{
    display: none;
  }
  .phone-hobby-text{
    display: block;
    width: 85%;
    height: 85%;
    text-align: left;
    font-size: 14px;
    letter-spacing: 1px;
    overflow: hidden;
  }
  .phone-hobby-text p:first-child {
    font-size: 17px;
    line-height: 2;
  }
  .phone-hobby-text p:not(:first-child){
    text-indent: 28px;
    line-height: 1.5;
  }
  .phone-hobby-text span{
    font-weight: bold;
  }
}
</style>



